let characters;

const getData = () => {
  fetch('https://www.breakingbadapi.com/api')
    .then((response) => response.json())
    .then((data) => {
      getCharacters(data);
      getDeaths(data);
      getEpisodes(data);
      getQuotes(data);
    });
};

const getCharacters = (data) => {
  fetch(data.characters)
    .then((response) => response.json())
    .then((dataCharacters) => {
      console.log(dataCharacters);
    });
};

const getDeaths = (data) => {
    fetch(data.deaths)
    .then((response) => response.json())
    .then((dataDeaths) => {
      console.log(dataDeaths);
    });
};

const getEpisodes = (data) => {
    fetch(data.episodes)
    .then((response) => response.json())
    .then((dataEpisodes) => {
      console.log(dataEpisodes);
    });
};

const getQuotes = (data) => {
    fetch(data.quotes)
    .then((response) => response.json())
    .then((dataQuotes) => {
      console.log(dataQuotes);
    });
};



module.exports = getData();
